.loading {
  height: 84vh;
  display: grid;
  justify-content: center;
  align-items: center;
}

.loader {
  border: 16px solid #2ecc70ab;
  border-top: 16px solid #3498db;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
